@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 20px 0;
    }

    .mkdf-comment {

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 13px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 86px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 100px;
        height: 100px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: 0;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 117px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 4px 0 2px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            position: relative;
            margin-right: 10px;
            font-size: 13px;
            font-family: $default-heading-font;
            text-transform: uppercase;
            font-style: normal;
            font-weight: 400;
            color: $first-main-color;
            letter-spacing: 0.2em;

            &:before{
                content: '';
                position: absolute;
                left: 50%;
                @include mkdfTransform(translateX(-50%));
                @include mkdfTransition(width 0.3s ease);
                bottom: -4px;
                height: 1px;
                width: 30px;
                background-color: currentColor;
            }

            &:hover{
                &:before{
                    width: 100%;
                }
            }

        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            float: left;
            margin: 0;
            font-size: 21px;
            color: $light-text-color;
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 69px 0 78px;

        .comment-form{
            .mkdf-btn.mkdf-btn-medium{
                padding: 8px 41px;
            }
        }
    }

    .comment-reply-title {
        margin: 0 0 28px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    .comment-form-cookies-consent{
        margin: 10px 0 20px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

input:focus::-webkit-textfield-decoration-container {
    visibility: hidden;
}

.form-submit,
.wpcf7-form{
    .mkdf-btn.mkdf-btn-solid.mkdf-btn-with-additional-outline{
        background-color: $first-main-color;
        border-color: $first-main-color;
        @include mkdfButtonAdditionalOutline($first-main-color);

        &:hover{
            @include mkdfButtonOutlineColor(!important);
        }

        .mkdf-ms-explorer &{
            border-color: $first-main-color !important;
            @include mkdfButtonAdditionalOutlineIE($first-main-color);
        }
    }
}

div.wpcf7{
    .wpcf7-form{
        .ajax-loader{
            display: block;
        }
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.mkdf-contact-form{
    &.mkdf-grid-tiny-gutter{
        margin: 0 -7px;
        >div{
            padding: 0 7px;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    .owl-item{
        &.active{
            z-index: 10;
            @include mkdfTransform(scale(1.0005));
        }
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            overflow: visible;

            @include mkdfTransform(translateY(-50%));
            @include mkdfTransition(color .2s ease-out);

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 45px;
                line-height: 1;
                color: $first-main-color;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }

        .mkdf-prev-icon,
        .mkdf-next-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width: 24px;
            height: 62px;
            color: $default-heading-color;
            line-height: 1;
            @include mkdfTransition(color .2s ease-out);

            .mkdf-top-part,
            .mkdf-bottom-part {
                display: block;
                position: absolute;
                left: 50%;
                width: 1px;
                height: 40px;
                background-color: currentColor;
                outline: 1px solid transparent;

                @include phone-portrait{
                    height: 25px;
                }
            }

            .mkdf-top-part {
                top: -3px;

                @include phone-portrait{
                    top: 10px;
                }

                &:after {
                    content: '';
                    position: absolute;
                    top: -12px;
                    left: 0;
                    height: 12px;
                    width: 1px;
                    background-color: inherit;
                    @include mkdfTransform(scaleY(0));
                    -webkit-transform-origin: 50% 100%;
                    transform-origin: 50% 100%;
                    -webkit-transition: -webkit-transform .2s;
                    transition: transform .2s;

                    @include phone-portrait{
                        top: -6px;
                        height: 6px;
                    }
                }
            }

            .mkdf-bottom-part {
                bottom: -3px;

                @include phone-portrait{
                    bottom: 10px;
                }

                &:after {
                    content: '';
                    position: absolute;
                    bottom: -12px;
                    left: 0;
                    height: 12px;
                    width: 1px;
                    background-color: inherit;
                    @include mkdfTransform(scaleY(0));
                    -webkit-transform-origin: 50% 0%;
                    transform-origin: 50% 0%;
                    -webkit-transition: -webkit-transform .2s;
                    transition: transform .2s;

                    @include phone-portrait{
                        bottom: -6px;
                        height: 6px;
                    }
                }
            }
        }

        .mkdf-prev-icon {
            .mkdf-top-part {
                @include mkdfTransform(rotate(45deg));
            }

            .mkdf-bottom-part {
                @include mkdfTransform(rotate(-45deg));
            }

            &:hover {
                .mkdf-top-part:after,
                .mkdf-bottom-part:after {
                    @include mkdfTransform(scaleY(1));
                }
            }
        }

        .mkdf-next-icon {
            .mkdf-top-part {
                @include mkdfTransform(rotate(-45deg));
            }

            .mkdf-bottom-part {
                @include mkdfTransform(rotate(45deg));
            }

            &:hover {
                .mkdf-top-part:after,
                .mkdf-bottom-part:after {
                    @include mkdfTransform(scaleY(1));
                }
            }
        }
        
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 8px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            overflow: visible; /*IE tweak*/

            @include ipad-portrait{
                margin: 0 10px;
            }

            span {
                display: inline-block;
                vertical-align: middle;
                width: 10px;
                height: 10px;
                border-radius: 0;
                background-color: transparent;
                border: 1px solid $first-main-color;
                box-sizing: border-box;

                @include mkdfTransform(rotate(45deg));
                @include mkdfTransition(background-color .2s ease-out, border .2s ease-out);

                @include ipad-portrait{
                    width: 12px;
                    height: 12px;
                }
            }

            &:hover,
            &.active {

                span {
                    background-color: $first-main-color;
                    border-color: $first-main-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .mkdf-404-title {
            margin: 0;
        }

        .mkdf-404-subtitle {
            margin: 35px 0 0;
        }

        .mkdf-404-text {
            margin: 10px 0 0;
        }

        .mkdf-btn {
            margin: 30px 0 0;

            &.mkdf-btn-solid:not(.mkdf-btn-custom-border-hover):not(.mkdf-btn-light-style){
                &:hover{
                    background-color: $first-main-color !important;
                    outline-color: $first-main-color !important;
                    border-color: $first-main-color !important;
                }
            }

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: calc(100vh - 70px);
            margin-top: 0!important;
        }

        &.admin-bar {
            .mkdf-content {
                height: calc(100vh - 102px);
            }
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 56px;
    right: 39px;
    bottom: 5px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 56px;
        height: 44px;
        line-height: 44px;
        font-size: 30px;
        text-align: center;
        color: $first-main-color;
        background-color: transparent;
        border: none;
        overflow: hidden;
    }

    .mkdf-btt-arrow {

        .mkdf-btt-left-part,
        .mkdf-btt-right-part {
            display: block;
            position: absolute;
            width: 1px;
            height: 30px;
            background-color: $first-main-color;
            outline: transparent solid 1px;
            transform-origin: 100% 100%;
            -webkit-transform-origin: 100% 100%;

            &:after {
                content: '';
                position: absolute;
                bottom: -12px;
                left: 0;
                height: 12px;
                width: 1px;
                background-color: inherit;
                -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
                @include mkdfTransform(scaleY(0));
                @include mkdfTransitionTransform(.2s);
            }
        }

        .mkdf-btt-left-part {
            left: 6px;
            @include mkdfTransform(rotate(45deg));
        }

        .mkdf-btt-right-part {
            left: 49px;
            top: -1px;
            @include mkdfTransform(rotate(-45deg));
        }

    }

    &:hover{
        .mkdf-btt-left-part,
        .mkdf-btt-right-part {

            &:after {
                @include mkdfTransform(scaleY(1));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
