footer {
    // widgets spacing
    $widget-bottom-margin: 45px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 15px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;
    $widget-search-border: 1px solid $default-border-color;

    // text
    $widget-heading-color: #fff;
    $widget-text-color: #c8c8c8;
    $widget-top-text-color: #c8c8c8;
    $widget-bottom-text-color: #b5b5b5;

    $widget-title-margin-bottom: 21px;

    // links
    $widget-link-color: $widget-top-text-color;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .mkdf-footer-top-holder{

        color: $widget-top-text-color;

        a,p{
            color: $widget-top-text-color;
        }

        a{
            &:hover{
                color: $first-main-color;
            }
        }
    }

    .mkdf-footer-bottom-holder{

        color: $widget-bottom-text-color;

        a,p{
            color: $widget-bottom-text-color;
            font-size: 17px;
        }

        a{
            &:hover{
                color: $first-main-color;
            }
        }

        .widget{
            margin-bottom: 0;
        }
    }

    .mkdf-section-title-holder{
        .mkdf-separator-holder{
            margin: 23px 0 16px;
        }
    }

    .widget{
        select{
            background-color: #000;
        }

        .mkdf-searchform{
            .input-holder {
                button{
                    background-color: transparent;
                }
            }
        }

        .tagcloud{
            a{
                padding: 0 13px 0 0;
            }
        }

    }
}