aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 45px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: #eeeeee;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 6px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 0px;
    $widget-list-item-border-style: solid;
    $widget-search-border: 1px solid $default-border-color;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: inherit;

    $widget-title-margin-bottom: 6px;

    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    text-align: center;

    .mkdf-separator-holder{
        &.mkdf-separator-advanced{
            margin: 24px 0 13px;
        }
    }

    .mkdf-blog-list-widget{
        .mkdf-blog-list-holder {
            margin-top: 4px;
            .mkdf-outer-space {
                margin: 0;
                .mkdf-item-space {
                    padding: 0;

                    .mkdf-bli-content{
                        .mkdf-post-info-date{
                            display: inline-block;
                            vertical-align: middle;
                            margin: 0;

                            a {
                                position: relative;
                                display: inline-block;
                                padding-right: 10px;
                                font-size: 21px;
                                font-family: inherit;
                                color: #000;
                                font-style: inherit;

                                &:hover{
                                    color: $first-main-color;
                                }

                                &:after {
                                    content: '/';
                                    position: absolute;
                                    right: 0;
                                    top: 0;
                                }
                            }
                        }
                        .mkdf-post-title{
                            display: inline-block;
                            vertical-align: middle;
                            font-size: 21px;
                        }
                    }
                }
            }
        }
    }
}